import { h } from 'preact'
import { render } from 'preact'
import CommentForm from './Form.jsx'
import Auth from '../Auth'
import AppUser from '../Auth/AppUser.jsx'
import List from './List.jsx'

class App extends Auth {
  constructor() {
    super()
    this.list = null
  }
  render() {
    return (               h('div',{},
      h(AppUser,{error:this.state.error,loading:this.state.loading,auth:this.state.auth,host:this.props.host,onSignOut:() => {
        this.setState({ auth: {} })
      }}),

      h(CommentForm,{host:this.props.host,path:`${this.props.host}/comment`,auth:this.state.auth,submitFinish:async (res) => {
        const { 'error': error, id } = await res.json()
        if (!error && id) {
          if (this.list) this.list.fetch(id)
        }
      }}),

      h(List,{host:this.props.host,ref:(e) => {
        this.list = e
      }}),

    ))
  }
}

window['comments'] = ({
  'host': host = 'https://api.artd.eco', 'container': container = 'preact',
}) => {
  render(h(App,{host:host}), document.getElementById(container))
}